<template>
  <section>

    <section>
      <div class="modal my-popups right fade show" id="manageProjectModal" role="dialog" style="display: block;">
        <div class="modal-dialog mw-1100" role="document">
          <div class="modal-content">
            <div class="modal-header mheader-user">
              <button type="button" class="close" @click="ClosePOp"><i class="fa fa-times"
                  aria-hidden="true"></i></button>
              <h4 class="modal-title" id="myModalLabel">{{ $t('AddDeployProduct') }}</h4>Add Deploy Product

            </div>

            <div class="modal-body">
              <div class="border p-3">
                <dynamic-form lang="en" v-if="DataLoaded" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form>

              </div>

              <div class="modal-footer">
                <div class="col-lg-12">
                  <small class="text-danger">Fields marked with an asterisk (*) are mandatory.</small>
                </div>


              </div>

            </div>


          </div>

        </div>
        <AddproducLocation v-if="Addlocation"  @closepopup="closepopup"></AddproducLocation>
      </div>
      

    </section>

  </section>
</template>
<script>
import DataService from '../../service/DataService';
import AddproducLocation from '../../components/Modal/AddproducLocation.vue';

export default {

  components: {
    AddproducLocation
  },

  data() {
    return {
      FormSchema: [
        {
          layoutType: "double",
          Data: [
            {
              astype: "RadioField",
              label: this.$t('DeployMentType'),
              name: "DeployMentType",
              value: "1",
              placeholder: "",
              config: {
                options: [{ value: 1, name: "Product" }, { value: 2, name: "Inventory" }],

                //CallBackFunction: null
              },

              validationRules: { "required": false },

            },

          ]
        },

        {
          layoutType: "double",
          Data: [


            {
              astype: "SelectField",
              label: this.$t('ProductType'),
              name: "ProductType",
              value: "",
              placeholder: "Select",
              config: {
                options:[],
                //  option_request_url:process.env.VUE_APP_API_URL+'/AssetAssignment/GetAssetType?type=&issueTo=&locationId=&requestType=ASSIGN&requestFrom=',
                //  token:this.GetUserInfo.Token,
                 showAddIcon: false,
                 onChange: this.onChangeDropdown,
              },
              validationRules: "required",
              dependent_show_type: 0
            },
            
            {
              astype: "SelectField",
              label: this.$t('ProductName'),
              name: "ProductName",
              value: "",
              placeholder: "",
              config: {
                options: [],
                showAddIcon: false,
                onAddButtonClick: function (e, field) {
                }
              },

            },
            {
              astype: "SelectField",
              label: this.$t('Location'),
              name: "Location",
              value: "",
              placeholder: "",
              config: {
                options: [],
                showAddIcon: true,

                onChange: function (e, field) {

                },
                onAddButtonClick: this.OpenPopup,
                validationRules: "required",

              }

            },

            {
              astype: "NumericField",
              label: this.$t('Quantity'),
              name: "Quantity",
              value: "0",
              placeholder: "",
              validationRules: { "required": true },
              config: {
                onChange: this.calcSum
              }



            },
            {
              astype: "NumericField",
              label: this.$t('UnitPrice'),
              name: "UnitPrice",
              value: "0",
              placeholder: "",
              validationRules: { "required": false },
              config: {

                onChange: this.calcSum
              }
            },
            {
              astype: "NumericField",
              label: this.$t('TotalCost'),
              name: "TotalCost",
              value: "0.00",
              placeholder: "",
              validationRules: { "required": false },

            },
          ]
        }
      ],

      buttons: [
        {
          type: "submit",
          class: "btn btn-success",
          text: "<i class='fa fa-save pr-2'></i>Save",
        },

        {
          type: "button",
          class: "btn btn-danger",
          text: "<i class='fa fa-close pr-2'></i>Cancel",
          onClick: this.ClosePopup
        }
      ],
      LocationData: [],
      myObj: [],
      ProductypeID: '',
      UserId: null,
      Addlocation: false,
      DataLoaded:0
    }
    
  },
  async created() {
    debugger
    var vm = this;
    (vm.FormSchema)[0].Data[0].value = 1
    await vm.GetLocation();
     await vm.BindItemTypeByUserId();
    // vm.GetCompanyAssestCatalogs();
    // vm.onChangeDropdown();
    // vm.submit();
    this.UserId = this.GetUserInfo.ID;
    vm.DataLoaded=1;
  },
  methods:
  {
    // GetCompanyAssestCatalogs: function (ths) {
    //   let url = `id=${ths}&issueTo=${this.UserId}&locationId=${this.txtLocation}&requestFrom=assetassignment`;
    //   DataService.GetCompanyAssestCatalogs(url).then(response => {
    //     this.ProducttData = response.data;
    //   });
    // },
    calcSum(val) {
      var vm = this
      let totalcost = 0;
      totalcost = (vm.FormSchema)[1].Data[3].value * (vm.FormSchema)[1].Data[4].value;
      (vm.FormSchema)[1].Data[5].value = totalcost;

    },
    async BindItemTypeByUserId () {
      
      let url = `type=&issueTo=&locationId=&requestType=ASSIGN&requestFrom=`;
      var vm = this;
      await DataService.AssignGetAssetType(url).then(response => {
        response.data.forEach((itm, index) => {
          (vm.FormSchema)[1].Data[0].config.options.push({
            name: itm.ASSETTYPE,
            value: itm.ASSETID
          })
          
        })
      });
    },
   async GetLocation() {
      
      var vm = this;
      await DataService.GetLocationByuserId().then(response => {
        vm.LocationData = response.data.data;
        vm.LocationData.forEach((itm, index) => {
          (vm.FormSchema)[1].Data[2].config.options.push({
            name: itm.LOCATION_NAME,
            value: itm.COMPANY_LOCATION_ID
          })


        })
      })
    },
    async onChangeDropdown(evt) {
      debugger
      var vm = this;
      var params = `assetTypeid=${evt.target.value}`
     await DataService.GetAssetCatalogueType(params)
        .then(function (response) {
          response.data.forEach((itm, index) => {
            (vm.FormSchema)[1].Data[1].config.options.push({
              name: itm.NAME,
              value: itm.ASSETID
            })
          })
        });
    },
    OpenPopup: function () {
      var vm = this
      vm.Addlocation = true
    },
    ClosePOp() {
      var vm = this;
      vm.$parent.ClosePOp()
    },
    closepopup() {
      var vm = this
      vm.Addlocation = false;
    },
    onSubmit(formfields, e) {

      debugger
      var ArrayData = [];
      let myObj = {
        DeployId: 0,
        AssetCatalogueId: parseInt(vm.$route.params.id),
        AssetTypeId: formfields.ProductType,
        LocationId: formfields.Location,
        LocationName: null,
        Quantity: formfields.Quantity,
        UnitPrice: formfields.UnitPrice,
        Model: null,
        Manufacturer: null,
        Vendor: null,
        PurchaseOrder: null,
        WarrantyDuration: null,
        DepreciationRuleId: 0,
        OrderDate: null,
        CalibrationFrequency: null,
        AssetCost: formfields.TotalCost,
        SalvageCost: null,
        ItemLife: null,
        InvoiceNumber: null,
        InvoiceDate: null,
        InsurerName: null,
        InsuranceRefNumber: null,
        InsuranceTo: null,
        DeployedBy: null,
        DeployedAt: "0001-01-01T00:00:00",
        EnableWarranty: false,
        EnableInsurance: false,
        EnableItemCost: false,
        EnableCalibration: false,
        EnableDepreciation: false,
        DeploymentType: formfields.RadioField
      }
      ArrayData.push(myObj);
      DataService.SaveDeploy(ArrayData).then(function (response) {
        debugger;
        console.log(response);
        var vm = this
        debugger;
        vm.ClosePOp();
        if (response.data.result == "success") {

          vm.ShowAlert(vm.$t('Add Product has been succesfully added',), "success", true, vm.$t("Alert"))


        }
      })

    }
  }
}
</script>